.history code{word-wrap:break-word;white-space:pre;font-family:monospace;line-height:100%;color:#0B8C8F}.history code>.diff-N{color:green}.history code>.diff-D{color:red}.history code>.diff-A,.history .diff-E{color:black}.history .negative{color:red}.history .positive{color:green}.history .blue{color:blue}.history dl{padding:0.5em}.history dt{float:left;clear:left;width:100px;font-weight:bold}.history dd{margin:0 0 0 110px;padding:0 0 0.5em 0}.history-item:nth-child(odd){background-color:#efefef}



